  <div id="userInfo" class="scrollTouch" :class="{hasHeader: $utils.showNavBar()}">
    <headerNav v-if="$utils.showNavBar()" @headerShare='headerShare'></headerNav>
    <inviteLoop class="inviteLoop" v-if="$route.path === '/invite_friend'"></inviteLoop>
    <div class="centerBox">
      <div class="c-top"></div>
      <div class="c-center">{{commonState.inviteVcoin * 10}}</div>
      <div class="c-bottom">({{commonState.inviteVcoin * 10 / 2}})</div>
    </div>
    <div class="inviteCode">
      <generateCode></generateCode>
      <div class="whatCode flex-row hoz-flex-end">
        <span @click="setShowCode"></span>
      </div>
    </div>
    <!--  -->
    <div class="inviteType">
      <!-- app -->
      <div class="showApp" v-if="$utils.isWx() || $config.source_style >= 6">
        <div class="topType flex-row">
          <div class="shareType" @click="setShareWay(7)">
            <div class="icon">
              <div class="iconSize friend">
                <img src="../../../assets/img/getfan.png" alt="">
                <div class="recommend_tag"></div>
              </div>
            </div>
            <div class="text"></div>
          </div>
          <div class="shareType" @click="setShareWay(4)">
            <div class="icon">
              <div class="iconSize qrcode">
                <img src="../../../assets/img/lockfan/getfanimg.png" alt="">
              </div>
            </div>
            <div class="text"></div>
          </div>
          <div class="shareType" @click="setShareWay(6)">
            <div class="icon">
              <div class="iconSize qq">
                <img src="../../../assets/img/weibo.png" alt="weibo">
              </div>
            </div>
            <div class="text"></div>
          </div>
        </div>
        <div class="topType browser flex-row">
           <div class="shareType" @click="setShareWay(3)">
            <div class="icon">
              <div class="iconSize qq">
                <img src="../../../assets/img/qq.png" alt="qq">
              </div>
            </div>
            <div class="text">QQ</div>
          </div>
          <div class="shareType" @click="setShareWay(2)">
            <div class="icon">
              <div class="iconSize wechat">
                <img src="../../../assets/img/wechat.png" alt="">
              </div>
            </div>
            <div class="text"></div>
          </div>
          <div class="shareType" @click="setShareWay(5)">
            <div class="icon">
              <div class="iconSize wechat">
                <img src="../../../assets/img/copybtn.png" alt="qq">
              </div>
            </div>
            <div class="text"></div>
          </div>
          
        </div>
      </div>
      <div class="centerType flex-row" v-else>
        <div class="shareType" @click="setShareWay(5)">
          <div class="icon">
            <div class="iconSize wechat">
              <img src="../../../assets/img/my/share_link.png" alt="qq">
            </div>
          </div>
