      </div>
    </div>
    <!--  -->
    <div class="rewardList">
      <div class="rewardNav flex-row">
        <div class="leftNav nav_text" :class="{active: activeIndex === 0}">
          <div class="Nav" @click="setIndex(0)"></div>
        </div>
         <div class="rightNav nav_text" @click="setIndex(1)" :class="{active: activeIndex === 1}">
          <div class="Nav"></div>
        </div>
      </div>
      <!--  -->
      <transition name='RightList'>
        <div v-if="activeIndex === 0">
          <div class="rewardRules">
            <div class="inviteReward "></div>
            <div class="inviteRewardTip"></div>
            <!-- <div class="inviteFirst flex-row invite1"><div class="bgIcon inviteBg"></div><span class="redColor">{{commonState.inviteVcoin * 10}}</span></div>
            <div class="inviteFirst flex-row invite2"><div class="bgIcon inviteBg"></div><span class="redColor">{{commonState.inviteVcoin * 10 / 2}}</span></div> -->
            <div class="inviteBox">
              <div class="invite-content">
                <div class="title"></div>
                <div class="invite-text">
                  <span class="redColor">{{commonState.inviteVcoin * 10}}</span><br>
                </div>
              </div>
              <div class="invite-content">
                <div class="title"></div>
                <div class="invite-text">
                  <span class="redColor">{{commonState.inviteVcoin * 10 / 2}}</span><br>
                </div>
              </div>
            </div>
            <div class="inviteTip "></div>
            <div class="inviteReward"></div>
            <div class="friendTip"></div>
            <div class="count flex-row">
              <!-- <div class="count_inner flex-row nowrap">({{rewards.friend_reading_bonus}}) = ({{rewards.friend_reading_bonus / 2}}) * 2</div> -->
              <div class="count_inner flex-row"> =  * {{(2 * commonState.inviteVcoin / 10).toFixed(2)}}</div>
