            <div class="skillTip skillTip1">3/qq<span class="redColor">200%</span></div>
            <div class="skillTip skillTip2"></div>
            <div class="skillTip skillTip3"><span class="redColor">{{commonState.inviteVcoin * 1}}</span></div>
            <div class="skillTip skillTip4"></div>
          </div>
        </div>
      </transition>
      <transition name='leftList'>
        <!--  -->
        <div class="inviteListBox" v-if="activeIndex === 1">
          <inviteList :list='inviteDataList'></inviteList>
