<script>
import headerNav from '@/components/task/header-nav'
import inviteLoop from '@/components/task/invite-friend/invite-loop'
import generateCode from '@/components/task/invite-friend/generateCode'
import whatCode from '@/components/task/invite-friend/whatCode'
import inviteList from '@/components/task/invite-friend/inviteList'
import ShareCommon from '@/components/share/shareCommon'
import IndexShare from '@/components/share/IndexShare'
import {mapState, mapMutations} from 'vuex'
export default {
  components: {
    headerNav,  // 
    inviteLoop, // 
    generateCode, // 
    whatCode,   // 
    inviteList, // 
    ShareCommon, // 
    IndexShare
  },
  computed: mapState({
    commonState: state => state.common
  }),
  data () {
    return {
      activeIndex: 0, // 0 1
      showCode: false, // 
      showAll: false, // true
      showShare: false, // 
      rewards: {},     // 
      inviteDataList: [], // 
      appShare: {
        count: 0
      }, // app
      shareParams: {} // 
    }
  },
  methods: {
    ...mapMutations([
      'common_login_box',
      'setShowShare',
      'set_shareSuccessObj',
      'set_shareType',
      'common_set_vcoin'
    ]),
    // 
    headerShare () {
      this.appShare.count++
    },
    // 
    getArc (e) {
      if (e) {
        this.shareParams = e
        console.log('this.shareParams' + JSON.stringify(this.shareParams))
      }
    },
    // app
    setCallBack ({type, way, success}) {
      this.shareSuccess({type, way, success})
    },
    // 
    shareSuccess (e) {
      this.$api.get('CALLBACK_REWARD', {tag: 'inviting_friend'}, res => {
        if (res.data.coin > 0) {
          let data = res.data
          data.type = this.commonState.shareArc['inviting_friend']
          this.set_shareType(7)
          this.set_shareSuccessObj({
            type: 7,
            shareSuccessObj: data
          })
          this.setShowShare(true)
        }
      })
    },
    // app
    showAppShare () {
      this.appShare.count++
    },
    /**
     * 
     * @param {Number} type   1 2  3 qq 4,  5 
     */
    setShareWay (type) {
      // if (type === 1) {
      //   let data = {
      //     btn_name: 'invite_friend_btn_share_moments_moments'
      //   }
      // } else if (type === 2) {
      //   let data = {
      //     btn_name: 'invite_friend_btn_share_moments_weixin'
      //   }
      // } else if (type === 3) {
      //   let data = {
      //     btn_name: 'invite_friend_btn_share_moments_qq'
      //   }
      // } else if (type === 6) {
      //   let data = {
      //     btn_name: 'invite_friend_btn_share_moments_sina'
      //   }
      // } else if (type === 4) {
      //   let data = {
      //     btn_name: 'invite_friend_btn_share_moments_picture'
      //   }
      // } else if (type === 5) {
      //   let data = {
      //     btn_name: 'invite_friend_btn_share_moments_url'
      //   }
      // }
      // this.$api.post('TOTAL_CLICKBTN', data, res => {})
      if (this.$config.source_style >= 6) {
        if (type <= 3) {
          let id = 'weixin'
          let msg = {
            title: this.shareParams.title,
            content: this.shareParams.desc,
            thumbs: [this.shareParams.img_url],
            href: this.shareParams.shareLink
          }
          if (type === 1) {
            id = 'weixin'
            msg.type = 'web'
            msg.extra = {
              scene: 'WXSceneTimeline'
            }
          } else if (type === 2) {
            id = 'weixin'
            msg.type = 'web'
            msg.extra = {
              scene: 'WXSceneSession'
            }
          } else if (type === 3) {
            id = 'qq'
            // msg.type = 'text'
          }
          this.commonState.shares[id].send(msg, res => {
            if (type === 3) {
              this.$toast({
                message: '',
                duration: 1500
              })
            }
            this.setCallBack({type: 'app', way: type})
          }, err => {
            if (err.code === -2) {
              this.$toast({
                message: ''
              })
              this.setCallBack({
                type: 'app',
                way: '3',
                success: 2
              })
            } else {
              this.$toast({
                message: '',
                duration: 1500
              })
            }
          })
        } else if (type === 4) {
          this.$router.push('/saveimage')
        } else if (type === 5) {
          this.$router.push('/short_link')
        } else if (type === 6) {
          let id = 'sinaweibo'
          let msg = {
            title: this.shareParams.title,
            content: this.shareParams.desc,
            thumbs: [this.shareParams.img_url],
            href: this.shareParams.shareLink
          }
          this.commonState.shares[id].authorize(() => {
            this.commonState.shares[id].send(msg, res => {
              if (type === 3) {
                this.$toast({
                  message: '',
                  duration: 1500
                })
              }
              this.setCallBack({type: 'app', way: type})
            }, err => {
              if (err.code === -2) {
                this.$toast({
                  message: ''
                })
                this.setCallBack({
                  type: 'app',
                  way: '3',
                  success: 2
                })
              } else {
                this.$toast({
                  message: '',
                  duration: 1500
                })
              }
            })
          })
        } else if (type === 7) {
          let msg = {
            title: this.shareParams.title,
            content: this.shareParams.desc,
            thumbs: [this.shareParams.img_url],
            href: this.shareParams.shareLink
          }
          this.$utils.plusReady(plus => {
            plus.share.sendWithSystem(msg, () => {
              this.setCallBack({type: 'app', way: type})
            }, (err) => {
              if (err.code === -2) {
                this.$toast({
                  message: ''
                })
                this.setCallBack({
                  type: 'app',
                  way: '3',
                  success: 2
                })
              } else {
                this.$toast({
                  message: '',
                  duration: 1500
                })
              }
            })
          })
        }
      //  
      } else if (this.$utils.isWx()) {
        if (type <= 3) {
          this.showShare = true
        } else if (type <= 4) {
          this.$router.push('/saveimage')
        } else {
          this.$router.push('/short_link')
        }
      // 
      } else {
        if (type <= 4) {
          this.$router.push('/saveimage')
        } else {
          this.$router.push('/short_link')
        }
      }
    },
    // 
    closeShare (e) {
      if (e) {
        // this.$api()
      }
      this.showShare = false
    },
    // url
    toUrl (url) {
      this.$router.push(url)
    },
    // 
    countList () {
      let arr = []
      for (let i = 0; i < 20; i++) {
        let tempData = {
          nickname: '7182',
          avatar: 'https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/default/avatar.png',
          create_time: '2018-10-08 20:03:50'
        }
        tempData.nickname = tempData.nickname + i
        arr.push(tempData)
      }
      return arr
    },
    // 
    getList () {
      this.$api.get('INVITE_LIST', {page: 1}, res => {
        this.inviteDataList = res.data.data.slice(0, 5)
        // this.inviteDataList = this.countList()
        // res.data.total = 100
        if (res.data.total >= 5) {
          this.showAll = true
        }
      })
    },
    getRewardRules () {
      this.$api.get('MESSAGE_HINT', {}, res => {
        this.common_set_vcoin(res.data.reward_coefficient)
      })
    },
    // 
    setIndex (index) {
      this.activeIndex = index
    },
    // 
    closePage () {
      this.showCode = false
    },
    // 
    setShowCode () {
      this.showCode = true
    },
    // 
    back () {
      this.$router.go(-1)
    }
  },
  mounted () {
    this.getList()
    this.getRewardRules()
    window.scrollTo(0, 0)
    // this.shareSuccess()
  }
}
</script>
